import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Atividades_manutencao } from '../../../modelo/Atividades_manutencao'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'atividades_manutencao-dados-edit',   templateUrl: 'atividades_manutencao_dadosEdit.component.html' }) export class Atividades_manutencaoDadosEditComponent implements OnInit {    atividades_manutencao: Atividades_manutencao;    private sub: Subscription;    private errorMessage: string;     constructor(private router: Router,               private route: ActivatedRoute,               private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.atividades_manutencao.Atividades_manutencao";                 let id = +params['id'];         this.atividades_manutencao = new Atividades_manutencao();         this.atividades_manutencao.cd_atividades_manutencao = id;         o.objetoJson = this.atividades_manutencao;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a atividades_manutencao. ';       }      this.atividades_manutencao = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   }     onSubmit() {         }     onCancel(event: Event) {     event.preventDefault();     this.router.navigate(['/']);   }  }


